(function () {
  'use strict';

  angular
    .module('spaStore.users')
    .controller('RegisterController', function (registerHelper, registerService, $timeout, $location, toastr) {
      var vm = this;

      vm.hasSecondInvoice = false;
      vm.errorMessage = '';
      vm.formIsInvalid = false;

      vm.registerForm = {
        username: 'Потребителско име:',
        password: 'Парола:',
        confirmPassword: 'Потвърди Парола:',
        email: 'Email адрес:',
        name: 'Име и фамилия:',
        phone: 'Телефон за контакт:',
        shippingAddress: 'Адрес за доставка:',
        invoiceDetails: 'Данни за фактура:',
        notes: 'Забележки:',
        subscribed: 'Записан за новини'
      };

      vm.userData = {};

      vm.registerInProgress = false;
      vm.registerUser = function () {
        if (!vm.registerInProgress) {
          vm.registerInProgress = true;
          var userModelIsValid = registerHelper.validateUserModel(vm.userData);

          if (typeof userModelIsValid != 'boolean') {
            vm.formIsInvalid = true;
            toastr.error(userModelIsValid);
            $timeout(function () {
              vm.formIsInvalid = false;
            }, 4000);
            vm.registerInProgress = false;
            return;
          }

          registerService.registerUser(vm.userData)
            .then(function (res) {
              vm.registerInProgress = false;
              toastr.success('Потребителят е регистриран успешно');
              $timeout(function () {
                $location.path('/dashboard');
              }, 2000);
            }, function (err) {
              vm.registerInProgress = false;
              console.log(err)
            })


        }


        console.log(vm.userData)
      }


    });
})();

(function () {
  'use strict';

  angular
    .module('spaStore')
    .controller('LoginController', function (loginService, toastr, $timeout, $location, commonMethods, $rootScope, userInfoService) {
      var vm = this;
console.log('here')
      vm.welcomeText = 'Добре дошли в kozmetikabg.eu! Моля, въведете портребителско име и парола!';

      vm.loginUser = function () {
        loginService.loginUser(vm.user)
          .then(function (res) {
            toastr.success('Успешно влизане');


            var DTO = {
              username: vm.user.username,
              token: res.token
            };

            if (vm.rememberMeClicked) {
              commonMethods.setObjectInLocalStorage('userInfo', DTO);
            } else {
              commonMethods.setObjectInSessionStorage('userInfo', DTO);
            }
            $rootScope.userInfo = userInfoService.getUserInfo();
            $timeout(function () {
              $location.path('/dashboard');
            }, 1000)
          }, function (err) {
            if (err == 'No such user.') {
              toastr.error('Няма такъв потребител');
            }

            if (err.message == 'Authentication failed. Wrong password.') {
              toastr.error('Грешна парола');
            }

          })
      }


    });
})();

